import { Injectable } from '@angular/core';
import { Card } from '../../shared/card/card.model';

@Injectable({providedIn: 'root'})

export class HandService {
  private hand: Card[]

  constructor() {}

  getHand() {
    return this.hand;
  }
  setHand(hand: Card[]) {
    this.hand = hand
  }
  addCard(card: Card) {
    this.hand.push(card)
  }
  removeCard(index: number) {
    this.hand.splice(index, 1)
  }
}
