import { Injectable } from '@angular/core';
import { Card } from '../shared/card/card.model';

@Injectable({providedIn: 'root'})

export class BattlefieldService {
  private melee: Card
  private ranged: Card
  private mental: Card

  constructor() {}

  getCard(row: string) {
    return this[row];
  }
  setCard(card: Card, row: string) {
    this[row] = card
  }
}
