export class Card {
  public name: string;
  public amount: number;
  public image: string;
  public attack: number
  public hp: number
  public cost: number
  public description: string
  public type: "ally" | "ability"
  public speed: "melee" | "ranged" | "mental"
  constructor(rawCard: RawCard) {
    this.name = rawCard.name;
    this.image = `assets/images/cards/${rawCard.name}.svg`;
    this.attack = rawCard.attack ? rawCard.attack : 0;
    this.hp = rawCard.hp ? rawCard.hp : 0;
    this.cost = rawCard.cost
    this.description = rawCard.description
    this.type = rawCard.type
    this.speed = rawCard.speed
  }
}

export interface RawCard {
  name: string
  attack?: number
  hp?: number
  cost: number
  description: string
  type: "ally" | "ability"
  speed: "melee" | "ranged" | "mental"
}
